import React, { Component } from 'react';
import { Col, Row, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import InputOnlyNumber from 'components/common/input/InputOnlyNumber';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormDocOut extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="V tr">{getFieldDecorator('positionName')(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="S nm nh k tng lng">
              {getFieldDecorator('frequencyYear')(<InputOnlyNumber />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormDocOut;



