import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, Btn } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import {
  getSalaryFrequencyConfig,
  deleteSalaryFrequencyConfig,
  downloadFile,
} from 'services/salaryFrequencyConfigService';
import { getQueryParams } from 'utils/urlUtil';
import FormSearch from './FormSearch';

class DocOutList extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteSalaryFrequencyConfig(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportDocOut = () => {
    const { paramSearch } = this.state;
    downloadFile(paramSearch);
  };

  render() {
    const { reload, paramSearch } = this.state;
    const columns = [
      {
        title: 'V tr',
        dataIndex: 'positionName',
        key: 'positionName',
      },
      {
        title: 'S nm nh k tng lng',
        dataIndex: 'frequencyYear',
        key: 'frequencyYear',
      },
      {
        title: 'H s tng nh k',
        dataIndex: 'frequencyPoint',
        key: 'frequencyPoint',
      },
      {
        title: 'H s khi im',
        dataIndex: 'firstLevelPoint',
        key: 'firstLevelPoint',
      },
      {
        title: 'S bc ti a',
        dataIndex: 'maxLevel',
        key: 'maxLevel',
      },

      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.TO_CHUC__QUAN_LY_CAN_BO__CAU_HINH_TANG_LUONG__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Nhp k lut">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group" style={{ justifyContent: 'flex-start' }}>
              <Link to={routeConstant.TO_CHUC__QUAN_LY_CAN_BO__CAU_HINH_TANG_LUONG__NHAP_DU_LIEU}>
                <BtnAddNew />
              </Link>

              <Btn onClick={this.exportDocOut} icon="download">
                Download
              </Btn>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getSalaryFrequencyConfig}
              paramSearch={paramSearch}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DocOutList />
  </Page>
);



